
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.container {
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.35);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
}

.container p {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.3px;
    margin: 20px 0;
}

.container span {
    font-size: 12px;
}

.container a {
    color: #333;
    font-size: 13px;
    text-decoration: none;
    margin: 15px 0 10px;
}

.container button {
    background-color: #4B62D6;
    color: #fff;
    font-size: 12px;
    padding: 10px 45px;
    border: 2px solid #D7D7D7;
    border-radius: 8px;
    font-weight: 600;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-top: 10px;
    cursor: pointer;
}

.container button.hidden {
    background-color: transparent;
    border-color: #fff;
}

.container form {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    height: 100%;
}

.container input {
    background-color: #eee;
    border: none;
    margin: 8px 0;
    padding: 10px 15px;
    font-size: 13px;
    border-radius: 8px;
    width: 100%;
    outline: none;
}

.formContainer {
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.signIn {
    left: 0;
    width: 50%;
    z-index: 2;
}

.container.active .signIn {
    transform: translateX(100%);
}

.signUp {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}

.container.active .signUp {
    transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    animation: move 0.6s;
}

@keyframes move {
    0%, 49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%, 100% {
        opacity: 1;
        z-index: 5;
    }
}


.socialIcons {
    margin: 20px 0;
}

.socialIcons a {
    border: 1px solid #ccc;
    border-radius: 8px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0 3px;
    width: 300px;
    height: 40px;
    justify-content: space-evenly;
}

.toggleContainer {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
    border-radius: 150px 0 0 100px;
    z-index: 1000;
}

.container.active .toggleContainer {
    transform: translateX(-100%);
    border-radius: 0 100px 150px 0;
}

.toggle {
    background-color: #4B62D6;
    height: 100%;
    color: #fff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.container.active .toggle {
    transform: translateX(50%);
}

.togglePanel {
    position: absolute;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 30px;
    text-align: center;
    top: 0;
    transform: translateX(0);
    transition: all 0.6s ease-in-out;
}

.toggleLeft {
    transform: translateX(-200%);
}

.container.active .toggleLeft {
    transform: translateX(0);
}

.toggleRight {
    right: 0;
    transform: translateX(0);
}

.container.active .toggleRight {
    transform: translateX(200%);
}

.barsContainer {
    position: relative; 
    width: 100%; 
    display: flex;
    justify-content: flex-start; 
    margin-top: 10px; 
  }
  
  .bar {
    border-radius: 5px;
    width: 100%; 
    background-color: red; 
    height: 15px; 
    position: absolute; 
    bottom: -5px; 
    left: 0; 
    transition: background-color 0.3s, height 0.3s; 
  }
  .passwordHint {
    color: #9c9a9a;
    font-size: 0.9rem; 
    margin-top: 5px; 
  }
  