
.container {
  width: 100vw;
  height: 100vh;
  background-color: #ddd;
}

.teste-componente-root {
    padding: 20px;
    background: linear-gradient(90deg, rgba(72,93,204,1) 41%, rgba(40,52,114,1) 100%);
    border: 1px solid #ddd;
    border-radius: 8px;
  }

  .btn-render{

    padding: 10px;
    border-radius: 10px; 
    background-color: rgba(72,93,204,1);
    cursor: pointer;
    color: white
    
  }
  
  .code-block {
    font-family: monospace;
    background: linear-gradient(90deg, rgba(72,93,204,1) 41%, rgba(40,52,114,1) 100%);
    padding: 16px;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  
  .button-container {
    margin-top: 20px;
  }
  
  .feedback-text {
    color: green;
    font-weight: bold;
    margin-top: 10px;
  }

  
  